@use "@querkmachine/felafel/src/scss/settings";
@use "helpers/colors";
@use "helpers/media-queries";
@use "helpers/typography";

.ip-my-dashboard {
	.bc-heading--jumbotron {
		margin-bottom: 1.25rem;
	}

	.bc-card__action {
		.bc-button {
			height: auto;
		}
	}

	.bc-card-media {
		flex-shrink: 0;
	}

	.bc-card__body {
		margin-top: 0;

		.bc-prose {
			font-size: 1.125rem;

			.bc-heading--title {
				font-weight: 300;
				color: colors.fs-color(brand-secondary);
				font-size: 2.125rem;
				line-height: 1.2941176471;
			}

			dt {
				font-weight: 600;
			}

			dd {
				margin-top: 0;
			}
			&.bc-card--profile-prose {
				@include media-queries.fs-mq($from: tablet) {
					display: flex;
				}
			}
			.bc-card--profile-welcome {
				display: flex;
				flex-direction: column;
				margin-top: 1rem;
				@include media-queries.fs-mq($from: tablet) {
					margin-top: 0;
					margin-left: 1rem;
				}
			}
		}
	}

	.bc-grid {
		grid-gap: 40px;
	}

	.bc-card--with-image {
		box-shadow: none;
		background-color: settings.$fs-canvas-background-color;
		padding: 2rem;

		@include media-queries.fs-mq($from: tablet) {
			display: flex;
		}

		.bc-prose > * + * {
			margin-top: 0.25rem;
		}

		.bc-card__media {
			flex-shrink: 0;
		}

		.bc-card__body {
			padding: 0;
		}

		.bc-card__inner {
			margin-top: 1rem;

			@include media-queries.fs-mq($from: tablet) {
				margin-left: 1rem;
				margin-top: 0;
			}

			@media only screen and (min-width: 1025px) {
				flex-direction: row;
			}
		}

		.bc-card__action {
			margin-left: 0;

			@media only screen and (min-width: 1025px) {
				margin-left: auto;
			}
		}
	}
	.ip-my-dashboard--courses {
		.bc-event-card {
			grid-template-columns: 1fr !important;
		}
	}
	&__recommends {
		background-color: settings.$fs-canvas-background-color;
		padding-block: 3rem;
		margin-top: 1.625rem;
		@include media-queries.fs-mq($from: tablet) {
			padding-block: 6rem;
			// margin-top: 0;
		}
		.bc-event-card--with-image {
			display: flex;
			padding: 0;
			height: 100%;
			flex-direction: column-reverse;
			.bc-event-card__media {
				padding: 0;
			}
			.bc-event-card__body {
				@include typography.fs-font-size(h3-mobile);
				flex-grow: 1;
				padding: 1rem;
				@media only screen and (min-width: 1025px) {
					@include typography.fs-font-size(h3);
					padding: 2rem;
				}
			}
			.bc-event-card__toolbar {
				margin-top: 2rem;
				margin-bottom: 3rem;
			}
			.bc-image__image {
				max-height: 256px;
				object-fit: cover;
			}
		}
		.bc-grid {
			margin-top: 1.5rem;
		}
		.bc-link__more {
			display: flex;
			justify-content: end;
			margin-top: 1.5rem;
			align-items: center;
			@include media-queries.fs-mq($from: tablet) {
				margin-top: 2rem;
			}
			.bc-icon {
				padding-left: 0.5rem;
			}
		}
	}
	&__recommends-intro {
		.bc-prose {
			padding-top: 1rem;
		}
		.bc-link__more {
			justify-content: start;
		}
	}
	&__events {
		margin-top: 1.5rem;
		@include media-queries.fs-mq($from: tablet) {
			margin-top: 3rem;
		}
	}
	&__courses {
		margin-top: 3rem;
		@include media-queries.fs-mq($from: tablet) {
			margin-top: 6rem;
		}
	}
	&__news {
		margin-block: 3rem;
		@include media-queries.fs-mq($from: tablet) {
			margin-block: 6rem;
		}
		.bc-news__grid {
			margin-top: 1.5rem;
		}
	}

	&--personalised {
		
	}
}
