@use "@querkmachine/felafel/src/scss/settings";
@use "helpers/media-queries";
@use "heading/heading";
@use "helpers/colors";

.bc-pricing-table {
	width: 100%;
	text-align: center;
	table-layout: fixed;
	border-collapse: collapse;
	background-color: colors.fs-color(white);
	th,
	td {
		vertical-align: top;
		font-size: 1rem;
		color: colors.fs-color(text);
		&,
		&::before {
			padding: 1rem;
		}
	}

	thead {
		th,
		td {
			background-color: settings.$fs-body-background-color;
			font-weight: normal;
		}
	}

	tbody {
		th,
		td {
			@extend .bc-heading--subtitle;
			display: table-cell;
		}
	}

	&__item {
		> * {
			display: block;
		}
		& + & {
			margin-top: 1.5rem;
		}
	}

	&__label {
		font-size: 0.875rem;
		color: colors.fs-color(text);
	}
}
